<template>
  <div class="particularss">
    <div class="first-line">
      <!-- 返回 -->
      <div @click="goBack">
        <img
          src="https://m.xiaomiyoupin.com/youpin/static/m/res/images/icons/icon_arrow_left_min_circle_gray.png"
        />
      </div>
      <!-- 左上角三... -->
      <div @click="multiplechoice = !multiplechoice">
        <img
          src="https://m.xiaomiyoupin.com/youpin/static/m/res/images/icons/icon_more_bold_solidgray.png"
        />
      </div>

      <div class="multiple-choice" v-show="multiplechoice">
        <div class="multiple-choice-one" @click="jumphome">
          <img
            src="https://m.xiaomiyoupin.com/youpin/static/m/res/images/product/detail_nav_icon_home.png"
          />
          <span>首页</span>
        </div>
        <div class="multiple-choice-one" @click="jumpcollege">
          <img
            src="https://m.xiaomiyoupin.com/youpin/static/m/res/images/icons/icon_heart_darkgray.png"
          />
          <span>我的收藏</span>
        </div>
      </div>
    </div>

    <!-- 轮播图 -->
    <div class="particulars-slideshow">
      <van-swipe @change="onChange" :autoplay="2500">
        <van-swipe-item v-for="(it, index) in items.imgs" :key="index"
          ><img :src="it" alt=""
        /></van-swipe-item>
        <template #indicator>
          <div class="custom-indicator">{{ current + 1 }}/5</div>
        </template>
      </van-swipe>
    </div>
    <!-- 价格和收藏 -->
    <div>
      <PriceTag
        :items="items"
        :collect="collect"
        @collectss="collectss"
      ></PriceTag>
    </div>
    <!-- 商品名 -->
    <div class="commodity-name">
      <div>{{ items.name }}</div>
    </div>
    <!-- 商品介绍 -->
    <div class="commodity-presentation">{{ items.summary }}</div>
    <!-- 新人礼包 -->
    <div class="new-gift-bag"></div>
    <div class="commodity-and-site">
      <!-- 选择商品 -->
      <div class="commodity-select">
        <van-cell is-link @click="showPopup" class="select"
          ><div>
            已选
            <span>{{ select }}</span>
          </div>
        </van-cell>
        <van-popup
          v-model="show"
          closeable
          close-icon="close"
          position="bottom"
          :style="{ height: '70%' }"
          class="popup"
        >
          <!-- 弹出层展示商品 -->
          <div class="commodity-exhibition">
            <img :src="items.pic_url" />
            <div class="we">
              <div>
                <span class="one">￥</span
                ><span class="two">{{ items.market_price / 100 }}</span>
              </div>
              <div class="one-name">{{ select }}</div>
            </div>
          </div>

          <div class="one-style">
            <div class="ic">样式</div>
            <div>
              <span
                v-for="(item, index) in selectstyle"
                :key="index"
                @click="clickselect(item, index)"
                :class="[{ pitchon: index === selectstyleone }]"
                >{{ item }}</span
              >
            </div>
          </div>

          <div class="quantity">
            <span>数量</span>
            <van-stepper
              v-model="value"
              theme="round"
              button-size="22"
              disable-input
              @change="onChanges"
            />
          </div>

          <div>
            <van-goods-action>
              <van-goods-action-button
                @click="AddShopping"
                type="warning"
                text="加入购物车"
              />
              <van-goods-action-button
                type="danger"
                text="立即购买"
                @click="Purchase"
              />
            </van-goods-action>
          </div>
        </van-popup>
      </div>
      <!-- 选择地址 -->
      <div class="site-select">
        <van-cell is-link @click="showPopupSite" class="delivery"
          >送至<span>{{ site }}</span></van-cell
        >
        <van-popup
          v-model="showa"
          closeable
          close-icon="close"
          position="bottom"
          :style="{ height: '70%' }"
          class="popup"
        >
          <div class="site-telter">配送地址</div>
          <van-address-list
            v-model="chosenAddressId"
            :list="list"
            default-tag-text="默认"
            add-button-text="选择其他配送地址"
            class="sites"
            @select="onSelect"
          />
        </van-popup>
      </div>
    </div>
    <!-- 商品导航 -->
    <div class="commodity-navigation">
      <van-goods-action>
        <van-goods-action-icon icon="shop-o" text="店铺" badge="" />
        <van-goods-action-icon
          icon="cart-o"
          text="购物车"
          :badge="shoppingNumber"
          @click="goshopping"
        />
        <van-goods-action-button
          @click="AddShopping"
          type="warning"
          text="加入购物车"
        />
        <van-goods-action-button
          type="danger"
          text="立即购买"
          @click="Purchase"
        />
      </van-goods-action>
    </div>
  </div>
</template>

<script>
import "../assets/sass/particulars.scss";
import PriceTag from "./PriceTag";
export default {
  data() {
    return {
      current: 0,
      selectstyle: ["样式1", "样式2", "样式3"],
      selectstyleone: "",
      items: {},
      multiplechoice: false,
      show: false,
      showa: false,
      select: "请选择商品",
      selectss: "",
      site: "选择地址",
      value: 1,
      chosenAddressId: "1",
      list: [],
      collect:
        "https://m.xiaomiyoupin.com/youpin/static/m/res/images/product/icon_product_unfavor.png",
      collectState: false,
      shoppingNumber: 0,
    };
  },
  created() {
    
    
    // 获取当前所点击的商品的详情
    this.items = JSON.parse(sessionStorage.getItem("particulars"));

    this.list = JSON.parse(sessionStorage.getItem("list"));
    // 调用判断该商品是否已收藏的方法
    this.estimate();
    // 判断购物车的数量
    let data = JSON.parse(sessionStorage.getItem("shopping"));
    this.shoppingNumber = data.length;
  },
  methods: {
    // 轮播图到第几页
    onChange(index) {
      this.current = index;
    },

    // 返回
    goBack() {
      this.$router.go(-1);
    },

    // 弹出选择商品层
    showPopup() {
      this.show = true;
    },

    // 设置登录才能选择地址
    showPopupSite() {
      let state = sessionStorage.getItem("loginstate");
      if (state == "false") {
        this.$dialog
          .confirm({
            title: "声明与政策",
            message:
              "欢迎您来到小米有品！\n我们依据最新法律法规要求，制定并更新了，《隐私政策》 《小米有品用户协议》以及《小米账号使用协议》\n您需阅读并同意相关政策条款方可进行登录。",
            messageAlign: "left",
          })
          .then(() => {
            this.$router.push({ path: "/loginandregister" });
          })
          .catch(() => {
            
          });
      } else {
        this.showa = true;
      }
    },

    // 跳到首页
    jumphome() {
      this.$router.push({ path: "/" });
    },
    // 选择地址
    onSelect(item, index) {
      
      this.chosenAddressId = index;
      this.site = this.list[this.chosenAddressId].address;
      this.showa = !this.showa;
    },

    // 选择商品样式
    clickselect(item, index) {
      if (this.selectstyleone == index) {
        this.selectstyleone = "";
        this.select = "请选择商品";
        this.selectss = "";
      } else {
        this.selectstyleone = index;
        this.selectss = item;
        this.select = item + "X" + this.value;
      }
    },

    // 商品数量改变时
    onChanges() {
      if (this.selectss !== "") {
        this.select = this.selectss + "X" + this.value;
      }
    },

    // 进入商品详情时判断是否已收藏
    estimate() {
      let data2 = JSON.parse(sessionStorage.getItem("collect"));
      
      for (let i = 0; i < data2.length; i++) {
        if (data2[i].gid == this.items.gid) {
          this.collect =
            "https://m.xiaomiyoupin.com/youpin/static/m/res/images/product/icon_product_favor.png";
          this.collectState = true;
          return;
        }
      }
    },
    // 点击收藏或取消收藏
    collectss() {
      if (this.collectState) {
        let data2 = JSON.parse(sessionStorage.getItem("collect"));
        for (let i = 0; i < data2.length; i++) {
          if (data2[i].gid == this.items.gid) {
            this.collect =
              "https://m.xiaomiyoupin.com/youpin/static/m/res/images/product/icon_product_unfavor.png";
            data2.splice(i, 1);
            sessionStorage.setItem("collect", JSON.stringify(data2));
            this.collectState = false;
            this.$toast.fail("已取消");
            return;
          }
        }
      } else {
        // 设置登录才能选择地址
        let state = sessionStorage.getItem("loginstate");
        if (state == "false") {
          this.$dialog
            .confirm({
            title: "声明与政策",
            message:
              "欢迎您来到小米有品！\n我们依据最新法律法规要求，制定并更新了，《隐私政策》 《小米有品用户协议》以及《小米账号使用协议》\n您需阅读并同意相关政策条款方可进行登录。",
            messageAlign: "left",
          })
          .then(() => {
            this.$router.push({ path: "/loginandregister" });
          })
          .catch(() => {
            
          });
        } else {
          let data2 = JSON.parse(sessionStorage.getItem("collect"));
          data2.push(this.items);
          sessionStorage.setItem("collect", JSON.stringify(data2));
          this.collect =
            "https://m.xiaomiyoupin.com/youpin/static/m/res/images/product/icon_product_favor.png";
          this.collectState = true;
          this.$toast.success("已收藏");
        }
      }
    },
    // 点击跳转到我的收藏
    jumpcollege() {
      let state = sessionStorage.getItem("loginstate");
        if (state == "false") {
          this.$dialog
            .confirm({
            title: "声明与政策",
            message:
              "欢迎您来到小米有品！\n我们依据最新法律法规要求，制定并更新了，《隐私政策》 《小米有品用户协议》以及《小米账号使用协议》\n您需阅读并同意相关政策条款方可进行登录。",
            messageAlign: "left",
          })
          .then(() => {
            this.$router.push({ path: "/loginandregister" });
          })
          .catch(() => {
            
          });
        }else{
          this.$router.push({ path: "/minecollect" });
        }
    },
    // 加入购物车
    AddShopping() {
      let data = JSON.parse(sessionStorage.getItem("shopping"));
      let state = sessionStorage.getItem("loginstate");
      if (state == "false") {
        this.$dialog
          .confirm({
            title: "声明与政策",
            message:
              "欢迎您来到小米有品！\n我们依据最新法律法规要求，制定并更新了，《隐私政策》 《小米有品用户协议》以及《小米账号使用协议》\n您需阅读并同意相关政策条款方可进行登录。",
            messageAlign: "left",
          })
          .then(() => {
            this.$router.push({ path: "/loginandregister" });
          })
          .catch(() => {
            
          });
      } else if (this.selectss == "") {
        this.$toast("请添加商品样式");
        this.show = true;
        return;
      } else {
        let data1 = this.items;
        this.$set(data1, "count", this.value);
        this.$set(data1, "stlie", this.selectss);
        
        data.push(data1);
        this.shoppingNumber = data.length;
        sessionStorage.setItem("shopping", JSON.stringify(data));
        this.$toast("已加入购物车");
        this.show = false;
      }
    },
    goshopping() {
      this.$router.push({ path: "/shoppingtrolley" });
    },
    Purchase() {
      let state = sessionStorage.getItem("loginstate");
      if (state == "false") {
        this.$dialog
          .confirm({
            title: "声明与政策",
            message:
              "欢迎您来到小米有品！\n我们依据最新法律法规要求，制定并更新了，《隐私政策》 《小米有品用户协议》以及《小米账号使用协议》\n您需阅读并同意相关政策条款方可进行登录。",
            messageAlign: "left",
          })
          .then(() => {
            this.$router.push({ path: "/loginandregister" });
          })
          .catch(() => {
            
          });
      } else if (this.selectss == "") {
        this.$toast("请添加商品样式");
        this.show = true;
        return;
      } else {
        let data = this.items;
        data.count=this.value;
         data.stlie=this.selectss;
        if (this.site == "选择地址") {
          data.site = '';
        }else{
          data.site=this.site;
        }
        sessionStorage.setItem('purchase',JSON.stringify([data]));
        // 
        this.$router.push({path:'/purchase'})
      }
    },
  },
  components: {
    PriceTag,
  },
};
</script>