import Vue from 'vue'
import VueRouter from 'vue-router'
import HomePage from '../views/HomePage.vue'
import Particulars from "../skip/Particulars.vue"
import { Dialog } from 'vant'

Vue.use(VueRouter)

// 解决重复点击路由报错
// Uncaught (in promise) NavigationDuplicated: Avoided redundant navigation to
// const originalPush = VueRouter.prototype.push
//    VueRouter.prototype.push = function push(location) {
//    return originalPush.call(this, location).catch(err => err)
// }

const routes = [{
    path: '/',
    name: 'Homepage',
    component: HomePage,
  },
  {
    path: '/classify',
    name: 'Slassify',
    component: () => import('../views/Classify.vue')
  },
  {
    path: '/shoppingtrolley',
    name: 'Shoppingtrolley',
    component: () => import('../views/ShoppingTrolley.vue')
  },
  {
    path: '/mine',
    name: 'Mine',
    component: () => import('../views/Mine.vue')
  },
  {
    path: '/particulars/:id',
    name: 'Particulars',
    props: true,
    component: Particulars
  },
  {
    path: '/searchpage',
    name: 'Searchpage',
    component: () => import('../SearchPage/SearchPage.vue')
  },
  {
    path: '/loginandregister',
    name: 'Loginandregister',
    component: () => import('../views/loginanregister/LoginAndRegister.vue')
  },
  {
    path: '/minesite',
    name: 'Minesite',
    component: () => import('../skip/MineSite.vue')
  },
  {
    path: '/addresstheeditor',
    name: 'Addresstheeditor',
    component: () => import('../skip/AddressTheEditor.vue')
  },
  {
    path: '/minecollect',
    name: 'Minecollect',
    component: () => import('../skip/MineCollect.vue')
  },
  {
    path:'/purchase',
    name:"Purchase",
    component: () => import('../skip/Purchase.vue')
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  
  if (to.name === 'Shoppingtrolley') {
    let state = sessionStorage.getItem('loginstate');
    
    if (state == 'false') {
        Dialog.confirm({
          title: "声明与政策",
          message: "欢迎您来到小米有品！\n我们依据最新法律法规要求，制定并更新了，《隐私政策》 《小米有品用户协议》以及《小米账号使用协议》\n您需阅读并同意相关政策条款方可进行登录。",
        })
        .then(() => {
          next({name:'Loginandregister'})
        })
        .catch(() => {
          
        });
    } else {
      next()
    }

  } else {
    next()
  }
})

export default router;