<template>
  <div id="app">
    <div id="nav">
      <router-link to="/" tag="div"
        ><i class="iconfont icon-home-g"></i><span>首页</span></router-link
      >
      <router-link to="/classify" tag="div"
        ><i class="iconfont icon-iconyihuifu-"></i
        ><span>分类</span></router-link
      >
      <router-link to="/shoppingtrolley" tag="div"
        ><i class="iconfont icon-qicheqianlian-1-copy"></i
        ><span>购物车</span></router-link
      >
      <router-link to="/mine" tag="div"
        ><i class="iconfont icon-wode"></i><span>我的</span></router-link
      >
    </div>
    <router-view :homepage="homepage" :recommend="recommend" />
  </div>
</template>

<script>
export default {
  data() {
    return {
      homepage: {
        slideshow: {},
        plaza: {},
        caption: {},
        crowdfunding_one: {},
        crowdfunding_two: {},
        crowdfunding_three: {},
        hot_commodity: {},
      },
      recommend: {
        data: {},
      },
    };
  },

  created() {
    // 网络请求获取首页信息
    this.getHomepageDataFromServer();
  },
  methods: {
    getHomepageDataFromServer() {

      let that = this;
      this.axios.get("https://api.kele8.cn/agent/https://www.xiaomiyoupin.com/homepage/main/v1002?platform=pc").then(function (response) {
         // 获取轮播图照片
          that.homepage.slideshow = response.data.data.homepage.floors[0].data.items;
          // 获取推荐照片
          that.homepage.plaza = response.data.data.homepage.floors[2].data.rows[0].items;
          //  获取每日新品
          that.homepage.caption = response.data.data.homepage.floors[8].data.items;
          // 获取小米有品众筹
          that.homepage.crowdfunding_one =
            response.data.data.homepage.floors[4].data.items[0];
          that.homepage.crowdfunding_two =
            response.data.data.homepage.floors[4].data.items[1];
          that.homepage.crowdfunding_three =
            response.data.data.homepage.floors[4].data.items[2];
          // 获取热销榜商品
          that.homepage.hot_commodity = response.data.data.homepage.floors[5].data.items;
          // 获取推荐商品
          that.recommend.data = response.data.data.recommend.floors[0].data;
      });
    },
  },
};
</script>

