import {
    Swipe,
    SwipeItem,
    Grid,
    GridItem,
    Dialog,
    Divider,
    Notify,
    Popup,
    Cell,
    GoodsAction,
    GoodsActionIcon,
    GoodsActionButton,
    Stepper,
    AddressList,
    AddressEdit,
    Toast,
    Card,
    NavBar,
    SwipeCell,
    CellGroup,
    Checkbox,
    CheckboxGroup,
    Button,
    SubmitBar,
    Empty,
    Tab,
    Tabs,
    Loading,
    Lazyload,
} from 'vant'

const vants = [
     Swipe,
     SwipeItem,
     Grid,
     GridItem,
     Dialog,
     Divider,
     Notify,
     Popup,
     Cell,
     GoodsAction,
     GoodsActionIcon,
     GoodsActionButton,
     Stepper,
     AddressList,
     AddressEdit,
     Toast,
     Card,
     NavBar,
     SwipeCell,
     CellGroup,
     Checkbox,
     CheckboxGroup,
     Button,
     SubmitBar,
     Empty,
     Tab,
     Tabs,
     Loading,
     Lazyload,
    ]

export default {
    /**
     * Vue.use 方法会自动调用install函数
     * Install Vant Component plugin
     * @param Vue
     */
    install(Vue) {
        vants.forEach(Component => {
            Vue.use(Component)
        })
    }
}